<template>
  <b-card title="รายการเลขปิดรับ-ส่วนลด">
    <div class="custom-search">
      <!-- advance search input -->
      <b-row class="align-items-end">
        <b-col md="3">
          <b-form-group>
            <label for="password">ประเภทหวย</label>
            <v-select
              v-model="filter.typeHuay"
              :options="optionsTypeHuay"
              label="lotto_type_name"
              placeholder="ประเภทหวย"
              :clearable="true"
              @input="getData()"
            />
          </b-form-group>
        </b-col>
        <b-col md="3">
          <b-form-group>
            <label for="password">เลขปิดรับ-ส่วนลด</label>
            <b-form-input
              id="price"
              type="text"
              placeholder="เลขปิดรับ"
              pattern="[0-9]*"
              v-model="form.closeNumber"
              maxlength="10"
              :disabled="filter.typeHuay == null"
              @input="getData()"
            />
          </b-form-group>
        </b-col>
        <b-col md="6">
          <b-form-group>
            <b-button
              v-ripple.400="'rgba(255, 255, 255, 0.15)'"
              type="button"
              variant="success"
              class="float-left"
              @click="handleSearch"
            >
              <feather-icon icon="SearchIcon" class="mr-50" />
              <span class="align-middle">ค้นหา</span>
            </b-button>
            <b-button
              v-ripple.400="'rgba(255, 255, 255, 0.15)'"
              type="button"
              variant="primary"
              class="float-right"
              exact
              @click="showModalAddPrize()"
            >
              <feather-icon icon="PlusIcon" class="mr-50" />
              <span class="align-middle">เพิ่มเลขปิดรับ</span>
            </b-button>
            <b-button
              v-ripple.400="'rgba(255, 255, 255, 0.15)'"
              type="button"
              variant="danger"
              class="float-right mr-2"
              exact
              @click="showModalDeletePrize()"
            >
              <feather-icon icon="TrashIcon" class="mr-50" />
              <span class="align-middle">ลบเลขปิดรับ</span>
            </b-button>
          </b-form-group>
        </b-col>
      </b-row>
    </div>

    <b-overlay
      :show="OverlayFlag"
      spinner-variant="primary"
      spinner-type="grow"
      spinner-small
      rounded="sm"
    >
      <div style="overflow-x: auto; padding: 0" v-if="rows ? rows.length : 0">
        <hr />
        <div v-for="(item, index) in getDataLottoType" :key="index">
          <div v-if="item.result ? item.result.length : 0">
            <h4 class="m-1">
              <b>{{ item.program }}</b>
            </h4>
            <div style="overflow: auto; height: 500px">
              <table class="table">
                <thead>
                  <tr class="text-center">
                    <th>ลำดับ</th>
                    <th>ประเภท</th>
                    <th>เลขปิดรับ</th>
                    <th>จำนวนที่ซื้อได้ (บาท)</th>
                    <th>คงเหลือ (บาท)</th>
                  </tr>
                </thead>
                <tbody>
                  <tr
                    v-for="(data, index) in item.result"
                    :key="index"
                    class="text-center"
                    :class="{
                      'bg-danger text-white': data.remaining_limit < 1,
                    }"
                    @click="showModalEditCloseNumber(data)"
                  >
                    <td>{{ index + 1 }}</td>
                    <td>{{ data.type }}</td>
                    <td>{{ data.number }}</td>
                    <td>{{ numberWithCommas(data.buy_limit) }}</td>
                    <td>{{ numberWithCommas(data.remaining_limit) }}</td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>
      <div class="text-center mt-3 mb-3" v-else>ไม่พบข้อมูล</div>
    </b-overlay>
    <validation-observer ref="addCredit">
      <b-modal
        id="modal-credit"
        ref="modalAddPrize"
        cancel-variant="outline-secondary"
        centered
        hide-footer
        no-close-on-backdrop
        title="เพิ่มเลขปิดรับ-ส่วนลด"
      >
        <b-form>
          <b-row>
            <b-col md="6">
              <b-form-group>
                <label for="email">ประเภทหวย:</label>
                <v-select
                  v-model="form.typeHuay"
                  :options="optionsTypeHuay"
                  label="lotto_type_name"
                  placeholder="ประเภทหวย"
                  :clearable="true"
                />
              </b-form-group>
            </b-col>
            <b-col md="6">
              <b-form-group>
                <label for="email">ประเภทเลข:</label>
                <v-select
                  v-model="form.typeNumber"
                  :options="optionsType"
                  label="lotto_type_name"
                  placeholder="ประเภทเลข"
                  :clearable="true"
                />
              </b-form-group>
            </b-col>
            <b-col md="6">
              <b-form-group>
                <label for="email">จำนวนที่ซื้อได้ (บาท):</label>
                <b-form-input
                  id="price"
                  type="text"
                  placeholder="จำนวนที่ซื้อได้"
                  pattern="[0-9]*"
                  v-model="form.buy_limit"
                  maxlength="10"
                />
              </b-form-group>
            </b-col>
            <b-col md="6">
              <div>
                <b-form-checkbox
                  v-model="allNumber"
                  value="1"
                  unchecked-value="0"
                  name="check-button"
                  inline
                >
                  ปิดรับทั้งหมด
                </b-form-checkbox>
              </div>
            </b-col>
            <b-col md="6">
              <div>
                <b-button
                  v-ripple.400="'rgba(255, 255, 255, 0.15)'"
                  type="button"
                  variant="primary"
                  class="mr-1"
                  exact
                  @click="space6back()"
                >
                  <!-- <feather-icon icon="PlusIcon" class="mr-50" /> -->
                  <span class="align-middle">กลับเลข</span>
                </b-button>
              </div>
            </b-col>
            <b-col md="12">
              <b-form-group>
                <label for="password">เลขปิดรับ</label>
                <b-form-textarea
                  id="internal-payment-note"
                  v-model="form.number"
                  placeholder="เลขปิดรับ (ถ้ามีเลขหลายตัวสามารถเว้นวรรคได้) เช่น 00 11 22 33"
                  rows="5"
                  trim
                  :disabled="allNumber === '1'"
                />
              </b-form-group>
            </b-col>
            <b-col md="12">
              <b-form-group>
                <div class="text-center">
                  <b-button
                    v-ripple.400="'rgba(255, 255, 255, 0.15)'"
                    type="button"
                    variant="success"
                    class="mr-1"
                    exact
                    @click="validationForm()"
                  >
                    <feather-icon icon="PlusIcon" class="mr-50" />
                    <span class="align-middle">เพิ่มเลขปิดรับ</span>
                  </b-button>
                </div>
              </b-form-group>
            </b-col>
          </b-row>
        </b-form>
      </b-modal>
    </validation-observer>
    <validation-observer ref="editForm">
      <b-modal
        id="modal-credit"
        ref="modalEditCloseNumber"
        cancel-variant="outline-secondary"
        centered
        hide-footer
        no-close-on-backdrop
        title="แก้ไขเลขปิดรับ-ส่วนลด"
      >
        <b-form>
          <b-row>
            <b-col md="4">
              <b-form-group>
                <label for="email">เลขปิดรับ:</label>
                <b-form-input
                  id="price"
                  type="text"
                  placeholder="ราคาจ่าย"
                  pattern="[0-9]*"
                  v-model="formedit.number"
                  maxlength="10"
                  disabled
                />
              </b-form-group>
            </b-col>
            <b-col md="4">
              <b-form-group>
                <label for="email">จำนวนที่ซื้อได้:</label>
                <b-form-input
                  id="price"
                  type="text"
                  placeholder="จำนวนที่ซื้อได้"
                  pattern="[0-9]*"
                  v-model="formedit.buy_limit"
                  maxlength="10"
                />
              </b-form-group>
            </b-col>
            <b-col md="4"> </b-col>
            <b-col md="12">
              <b-form-group>
                <div class="text-center">
                  <b-button
                    v-ripple.400="'rgba(255, 255, 255, 0.15)'"
                    type="button"
                    variant="success"
                    class="mr-1"
                    exact
                    @click="validationEditForm()"
                  >
                    <feather-icon icon="PlusIcon" class="mr-50" />
                    <span class="align-middle">แก้ไขเลขปิดรับ</span>
                  </b-button>
                  <b-button
                    v-ripple.400="'rgba(255, 255, 255, 0.15)'"
                    type="button"
                    variant="danger"
                    exact
                    @click="validationDeleteForm()"
                  >
                    <feather-icon icon="TrashIcon" class="mr-50" />
                    <span class="align-middle">ลบ</span>
                  </b-button>
                </div>
              </b-form-group>
            </b-col>
          </b-row>
        </b-form>
      </b-modal>
    </validation-observer>
    <validation-observer ref="deleteCredit">
      <b-modal
        id="modal-credit"
        ref="modalDeletePrize"
        cancel-variant="outline-secondary"
        centered
        hide-footer
        no-close-on-backdrop
        title="ลบเลขปิดรับ"
      >
        <b-form>
          <b-row>
            <b-col md="6">
              <b-form-group>
                <label for="email">ประเภทหวย:</label>
                <v-select
                  v-model="form.typeHuay"
                  :options="optionsTypeHuay"
                  label="lotto_type_name"
                  placeholder="ประเภทหวย"
                  :clearable="true"
                />
              </b-form-group>
            </b-col>
            <b-col md="6">
              <b-form-group>
                <label for="email">ประเภทเลข:</label>
                <v-select
                  v-model="form.typeNumber"
                  :options="optionsType"
                  label="lotto_type_name"
                  placeholder="ประเภทเลข"
                  :clearable="true"
                />
              </b-form-group>
            </b-col>
            <b-col md="12">
              <b-form-group>
                <div class="text-center">
                  <b-button
                    v-ripple.400="'rgba(255, 255, 255, 0.15)'"
                    type="button"
                    variant="danger"
                    class="mr-1"
                    exact
                    @click="validationDeleteForm()"
                  >
                    <feather-icon icon="TrashIcon" class="mr-50" />
                    <span class="align-middle">ลบเลขปิดรับ</span>
                  </b-button>
                </div>
              </b-form-group>
            </b-col>
          </b-row>
        </b-form>
      </b-modal>
    </validation-observer>
  </b-card>
</template>

<script>
import {
  BPagination,
  BFormGroup,
  BFormSelect,
  BDropdownItem,
  BDropdown,
  BRow,
  BCol,
  BButton,
  BCard,
  BFormInput,
  BModal,
  BFormTextarea,
  BForm,
  BOverlay,
} from "bootstrap-vue";
import { VueGoodTable } from "vue-good-table";
import Ripple from "vue-ripple-directive";
import store from "@/store/index";
import { HTTP } from "@/services/http-common";
import ToastificationContent from "@core/components/toastification/ToastificationContent.vue";
import { BFormCheckbox } from "bootstrap-vue";
import vSelect from "vue-select";
import { ValidationProvider, ValidationObserver } from "vee-validate";
import { required, integer } from "@validations";
import moment from "moment";

export default {
  components: {
    VueGoodTable,
    BPagination,
    BFormGroup,
    BFormInput,
    BFormSelect,
    BDropdownItem,
    BDropdown,
    BCol,
    BRow,
    BButton,
    BCard,
    BFormCheckbox,
    BModal,
    BFormTextarea,
    BForm,
    vSelect,
    ValidationProvider,
    ValidationObserver,
    BOverlay,
  },
  directives: {
    Ripple,
  },
  data() {
    return {
      modelId: this.$route.params.modelId,
      log: [],
      isLoading: false,
      currentPage: 1,
      pageLength: 10,
      totalRecords: 0,
      OverlayFlag: false,
      sort: [
        {
          field: "",
          sort: "",
        },
      ],
      dir: false,
      pages: ["5", "10", "25"],
      columns: [
        {
          thClass: "text-center",
          tdClass: "text-center",
          label: "ลำดับ",
          field: "num",
        },
        {
          thClass: "text-center",
          tdClass: "text-center",
          label: "หวย",
          field: "lotto_type_name",
        },
        {
          thClass: "text-center",
          tdClass: "text-center",
          label: "เลขปิดรับ",
          field: "number",
        },
        {
          thClass: "text-center",
          tdClass: "text-center",
          label: "ประเภทเลข",
          field: "type",
        },
        {
          thClass: "text-center",
          tdClass: "text-center",
          label: "วันที่ปิดรับ",
          field: "date_close",
        },
        // {
        //   thClass: "text-center",
        //   tdClass: "text-center",
        //   label: "ออกผล",
        //   field: "status_prize",
        // },
        // {
        //   thClass: "text-center",
        //   tdClass: "text-center",
        //   label: "เครดิต",
        //   field: "credit",
        //   width: "120px",
        //   sortable: false,
        // },
        {
          thClass: "text-center",
          tdClass: "text-center",
          label: "",
          field: "action",
          sortable: false,
          width: "64px",
        },
      ],
      rows: [],
      searchTerm: "",
      filter: {
        subject: "",
        startDate: "",
        endDate: "",
        typeHuay: null,
      },
      form: {
        prize3top: "",
        prize2bottom: "",
        typeHuay: "",
        typeNumber: "",
        type: "",
        type3top: "3 ตัวบน",
        type2bottom: "2 ตัวล่าง",
        closeNumber: "",
      },
      formedit: {
        pay: "",
        pay2: "",
        pay3: "",
        pay4: "",
        pay4: "",
        buy_limit: "",
        buy_limit2: "",
        buy_limit3: "",
        buy_limit4: "",
        buy_limit5: "",
        cn_id: "",
        closeNumber: "",
      },
      optionsType: [
        // "4 ตัวบน",
        // "4 ตัวโต๊ด",
        "3 ตัวบน",
        "3 ตัวโต๊ด",
        // "3 ตัวล่าง",
        "2 ตัวบน",
        "2 ตัวล่าง",
        "วิ่งบน",
        "วิ่งล่าง",
      ],
      optionsTypeHuay: [],
      required,
      integer,
      dataCredit: "",
      dataDisCredit: "",
      max: 3,
      closeNumber: [],
      allNumber: "0",
    };
  },
  computed: {
    direction() {
      if (store.state.appConfig.isRTL) {
        // eslint-disable-next-line vue/no-side-effects-in-computed-properties
        this.dir = true;
        return this.dir;
      }
      // eslint-disable-next-line vue/no-side-effects-in-computed-properties
      this.dir = false;
      return this.dir;
    },
    getDataLottoType: function () {
      const lotto = [];
      // const matches = this.option.map((item) => item.lotto_type_name);
      // let program = [...new Set(matches)];
      for (let i = 0; i < this.optionsType.length; i++) {
        const item = this.optionsType[i];
        const result = this.rows.filter((el) => el.type === item);
        lotto.push({ program: item, result });
      }
      return lotto;
    },
  },
  created() {
    this.getData();
  },
  mounted() {
    this.fetchTypeHuay();
  },
  methods: {
    numberWithCommas(x) {
      let sum = 0;
      if (x) {
        sum = x;
        return (
          parseFloat(sum)
            // .toFixed(2)
            .toString()
            .replace(/\B(?=(\d{3})+(?!\d))/g, ",")
        );
      }
      return x != null ? x : "-";
    },
    getData() {
      this.OverlayFlag = true;
      HTTP.get("api/close-number/all-agent", {
        params: {
          lotto_type_id:
            this.filter.typeHuay != null
              ? this.filter.typeHuay.lotto_type_id
              : undefined,
          number: this.form.closeNumber ? this.form.closeNumber : undefined,
        },
      })
        .then((response) => {
          this.rows = response.data.data;
          this.totalRecords = response.data.data.total;
          // console.log(this.rows)
          this.OverlayFlag = false;
        })
        .catch((error) => {
          this.$toast({
            component: ToastificationContent,
            position: "top-right",
            props: {
              title: "แจ้งเตือน",
              text:
                error.response.data.msg !== undefined
                  ? error.response.data.msg
                  : error.status,
              icon: "XCircleIcon",
              variant: "danger",
            },
          });
          this.OverlayFlag = false;
        });
    },
    fetchTypeHuay() {
      HTTP.get("api/lotto-type/all", {})
        .then((response) => {
          this.optionsTypeHuay = response.data.data;
        })
        .catch((error) => {
          this.$toast({
            component: ToastificationContent,
            position: "top-right",
            props: {
              title: "แจ้งเตือน",
              text:
                error.response.data.msg !== undefined
                  ? error.response.data.msg
                  : error.status,
              icon: "XCircleIcon",
              variant: "danger",
            },
          });
        });
    },
    validationForm() {
      this.OverlayFlag = true;
      if (this.form.number != undefined) {
        const numberArray = this.form.number.split(" ");
        if (numberArray.length <= 1) {
          // if (this.form.typeNumber == "4 ตัวบน") {
          //   const arr = numberArray[0].match(/.{1,4}/g);
          //   this.closeNumber = arr;
          // } else if (this.form.typeNumber == "4 ตัวโต๊ด") {
          //   const arr = numberArray[0].match(/.{1,4}/g);
          //   this.closeNumber = arr;
          // } else
          if (this.form.typeNumber == "3 ตัวบน") {
            const arr = numberArray[0].match(/.{1,3}/g);
            this.closeNumber = arr;
          } else if (this.form.typeNumber == "3 ตัวโต๊ด") {
            const arr = numberArray[0].match(/.{1,3}/g);
            this.closeNumber = arr;
          }
          // else if (this.form.typeNumber == "3 ตัวล่าง") {
          //   const arr = numberArray[0].match(/.{1,3}/g);
          //   this.closeNumber = arr;
          // }
          else if (this.form.typeNumber == "2 ตัวบน") {
            const arr = numberArray[0].match(/.{1,2}/g);
            this.closeNumber = arr;
          } else if (this.form.typeNumber == "2 ตัวล่าง") {
            const arr = numberArray[0].match(/.{1,2}/g);
            this.closeNumber = arr;
          }
          //  else if (this.form.typeNumber == "3 ตัวหลัง") {
          //   const arr = numberArray[0].match(/.{1,3}/g);
          //   this.closeNumber = arr;
          // }
        } else {
          this.closeNumber = numberArray;
        }
      }

      this.$refs.addCredit.validate().then((success) => {
        if (success) {
          HTTP.post(`api/close-number/add-close-number-agent`, {
            lotto_type_id:
              this.form.typeHuay != null
                ? this.form.typeHuay.lotto_type_id
                : undefined,
            number: this.closeNumber,
            type: this.form.typeNumber,
            buy_limit: this.form.buy_limit ? this.form.buy_limit : undefined,
            allNumber: this.allNumber,
          })
            .then((resp) => {
              this.showToast(
                resp.data.msg != null ? resp.data.msg : "สำเร็จ",
                "success"
              );
              this.$refs.modalAddPrize.hide();
              this.getData();
              this.OverlayFlag = false;
            })
            .catch((error) => {
              this.showToast(error.response.data.msg, "danger");
              this.OverlayFlag = false;
            });
        }
      });
    },
    validationDeleteForm() {
      // const numberArray = this.form.number.split(" ");
      this.$refs.deleteCredit.validate().then((success) => {
        if (success) {
          HTTP.delete(`api/close-number/delete-close-number-agent`, {
            data: {
              lotto_type_id:
                this.form.typeHuay != null
                  ? this.form.typeHuay.lotto_type_id
                  : undefined,
              // number: numberArray,
              cn_id: this.formedit.cn_id ? this.formedit.cn_id : undefined,
              type: this.form.typeNumber ? this.form.typeNumber : undefined,
            },
          })
            .then((resp) => {
              this.showToast(
                resp.data.msg != null ? resp.data.msg : "สำเร็จ",
                "success"
              );
              this.$refs.modalEditCloseNumber.hide();
              this.getData();
            })
            .catch((error) => {
              this.showToast(error.response.data.msg, "danger");
            });
        }
      });
    },
    validationEditForm() {
      this.$refs.editForm.validate().then((success) => {
        if (success) {
          HTTP.put(`api/close-number/edit-close-number-agent`, {
            cn_id: this.formedit.cn_id,
            buy_limit: this.formedit.buy_limit ? this.formedit.buy_limit : 0,
          })
            .then((resp) => {
              this.showToast(
                resp.data.msg != null ? resp.data.msg : "สำเร็จ",
                "success"
              );
              this.$refs.modalEditCloseNumber.hide();
              this.getData();
            })
            .catch((error) => {
              this.showToast(error.response.data.msg, "danger");
            });
        }
      });
    },
    func6back(number) {
      var num = number; //ตัวเลขที่ต้องการหาโต๊ด
      var textnum = num.toString(); //แปลงตัวเลขเป็นตัวอักษร
      var numlv1 = []; //ประกาศตัวแปลให้เป็น Array
      var numlv2 = [];
      var result = [];
      //จัดการ level 1 โดยการสลับตัวเลข 2 หลักซ้ายสุด
      numlv1[0] = textnum.substr(0, 1) + textnum.substr(1, 1);
      numlv1[1] = textnum.substr(1, 1) + textnum.substr(0, 1);
      //จัดการ level 2
      var endnum = textnum.substr(2, 1); //จำเลขตัวสุดท้าย
      for (var i = 0; i <= 2 - 1; i++) {
        //วนลูปการแทรกตัวเลข ทั้ง 2 ตัวเลขจาก level 1
        numlv2[0] = numlv1[i].substr(0, 1); //แยกตัวเลข หลักแรกออกมา จากตัวเลข level 1
        numlv2[1] = numlv1[i].substr(1, 1); //แยกตัวเลข หลักที่ 2 ออกมา จากตัวเลข level 1
        result.push(
          endnum + numlv2[0] + numlv2[1],
          numlv2[0] + endnum + numlv2[1],
          numlv2[0] + numlv2[1] + endnum
        );
      }
      let dup = [...new Set(result)];
      return dup;
    },
    space6back() {
      if (this.form.number != undefined) {
        const arr = this.form.number.split(" ");
        const arrLotto = [];
        this.form.number = "";
        arr.forEach((item) => {
          let result = this.func6back(item);

          result.forEach((el) => {
            this.form.number += el + " ";
            arrLotto.push(el);
          });
          console.log(this.form.number);
        });
        // this.form.number = arrLotto;
        this.closeNumber = arrLotto;
        // console.log(arr);
      }
    },
    handleSearch() {
      this.getData();
    },
    handleChangePage(page) {
      this.currentPage = page;
      this.getData();
    },
    handlePageChange(active) {
      this.perPage = active;
      this.getData();
    },
    onSortChange(params) {
      this.sort[0].field = params[0].field;
      this.sort[0].sort = params[0].type;
      this.getData();
    },
    showModalAddPrize() {
      this.form.credit = 0;
      this.form.note = "";
      this.form.type = "";
      //   this.dataCredit = data;
      this.$refs.modalAddPrize.show();
    },
    showModalEditCloseNumber(data) {
      this.formedit = data;
      this.form.typeHuay = "";
      //   this.dataCredit = data;
      this.$refs.modalEditCloseNumber.show();
    },
    showModalDeletePrize() {
      // this.form.credit = 0;
      // this.form.note = "";
      // this.form.type = "";
      //   this.dataCredit = data;
      this.$refs.modalDeletePrize.show();
    },
    // showModalDisCredit(data) {
    //   this.form.credit = 0;
    //   this.form.note = "";
    //   this.form.type = "";
    //   this.dataDisCredit = data;
    //   this.$refs.modalDiscredit.show();
    // },
    showToast(text, variant) {
      this.$toast({
        component: ToastificationContent,
        position: "top-right",
        props: {
          title: "แจ้งเตือน...",
          text: text,
          icon: variant === "success" ? "CheckIcon" : "BellIcon",
          variant: variant,
        },
      });
    },
    formatDateDay(date) {
      return moment(date).format("DD-MM-YYYY");
    },
    formatDate(date) {
      return moment(date).format("DD-MM-YYYY HH:mm:ss");
    },
  },
};
